import React, { useState } from "react";
import { Link as RouterLink } from "react-router-dom";
import PropTypes from "prop-types";
import clsx from "clsx";
import moment from "moment";
import {
  Avatar,
  Box,
  Card,
  CardMedia,
  Divider,
  Grid,
  IconButton,
  Link,
  SvgIcon,
  Tooltip,
  Typography,
  colors,
  makeStyles,
} from "@material-ui/core";
import { Rating } from "@material-ui/lab";
import FavoriteIcon from "@material-ui/icons/Favorite";
import FavoriteBorderIcon from "@material-ui/icons/FavoriteBorder";
import { Users as UsersIcon } from "react-feather";
import getInitials from "src/utils/getInitials";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";
import { useHistory } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  root: {},
  media: {
    height: 200,
    backgroundColor: theme.palette.background.dark,
  },
  likedButton: {
    color: colors.red[600],
  },
  subscribersIcon: {
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(1),
  },
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    // border: "1px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
    borderRadius: 10,
  },
}));

function ProjectCard({ project, className, handleDelete, ...rest }) {
  const classes = useStyles();
  const history = useHistory();

  const [isLiked, setLiked] = useState(project.isLiked);
  const [likes, setLikes] = useState(project.likes);
  const [open, setOpen] = React.useState(false);
  const handleEdit = (item) => {
    history.push({
      pathname: "/app/management/project/edit/" + item.pathSEO,
      state: item,
    });
  };
  const handleDeleteItem = () => {
    setOpen(false);
    handleDelete(project);
  };

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const handleLike = () => {
    setLiked(true);
    setLikes((prevLikes) => prevLikes + 1);
  };

  const handleUnlike = () => {
    setLiked(false);
    setLikes((prevLikes) => prevLikes - 1);
  };

  return (
    <Card className={clsx(classes.root, className)} {...rest}>
      <Box p={3}>
        <CardMedia className={classes.media} image={project.thumbnail} />
        <Box display="flex" alignItems="center" mt={2}>
          <Box>
            <Link
              color="textPrimary"
              component={RouterLink}
              to="#"
              variant="h5"
            >
              {project.name}
            </Link>
            <Typography variant="body2" color="textSecondary">
              Updated {moment(Number.parseFloat(project.datePosted)).fromNow()}
            </Typography>
          </Box>
        </Box>
      </Box>
      <Box px={3}>
        <Typography color="textSecondary" variant="body2">
          {project.address}
        </Typography>
        <Typography color="textSecondary" variant="body2">
          Khu vực : {project.areaName}
        </Typography>
      </Box>
      <Box py={2} px={3} textAlign="right">
        <Grid alignItems="center" container justify="space-between" spacing={3}>
          <Grid item>
            {project.highlight === 1 ? (
              <Typography
                color="textSecondary"
                variant="body2"
                style={{ color: "#db3131" }}
              >
                Trạng thái : Nổi bật
              </Typography>
            ) : (
              <Typography color="textSecondary" variant="body2">
                Trạng thái : Không nổi bật
              </Typography>
            )}
          </Grid>
          <Box flexGrow={1} />
          <Grid item>
            <button
              type="button"
              class="btn btn-primary"
              style={{ marginRight: 20 }}
              onClick={() => {
                handleEdit(project);
              }}
            >
              Sửa
            </button>
            <button type="button" class="btn btn-danger" onClick={handleOpen}>
              Xoá
            </button>
          </Grid>
        </Grid>
      </Box>
      <Divider />
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <div class="modal-dialog modal-sm" role="document">
          <div class="modal-content">
            <div class="modal-header" style={{ display: "flex" }}>
              <h5
                class="modal-title"
                id="exampleModalLabel"
                style={{ flex: 1 }}
              >
                Thông báo
              </h5>
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                aria-label="Close"
                onClick={handleClose}
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">Xác nhận xoá</div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                onClick={handleClose}
                data-dismiss="modal"
              >
                Đóng
              </button>
              <button
                type="button"
                class="btn btn-primary"
                onClick={handleDeleteItem}
              >
                Xác nhận
              </button>
            </div>
          </div>
        </div>
      </Modal>
    </Card>
  );
}

ProjectCard.propTypes = {
  className: PropTypes.string,
  project: PropTypes.object.isRequired,
};

export default ProjectCard;
